import React from 'react';
import { Stage, Layer, Rect, Text, Line, KonvaNodeEvents } from 'react-konva';
import Konva from 'konva';
import { objs } from './Coordinates';
import { withWindowSize, WithWindowSizeProps } from './resizer'

export const Plan: React.FC<{ selected: string | undefined, setSelected: (id: string) => void } & WithWindowSizeProps> = 
({ selected, setSelected, height, width }) => {
    const [moused, setMoused] = React.useState<string | undefined>(undefined);
    const [stageSize, setStageSize] = React.useState<{width: number, height: number}>({ width: 0, height: 0 });

    const divRef = React.useRef<HTMLDivElement>(null);

    const handleResize = () => {
        console.log('resize')
        divRef.current && setStageSize({ height: divRef.current.offsetHeight, width: divRef.current.offsetWidth});
    }

    React.useEffect(() => {
        
        window.addEventListener("resize", handleResize);
        return () => {
            window.removeEventListener("resize", handleResize);
        }
    }, []);

    const onMouseOver = ({ target: { attrs: { id } } }: Konva.KonvaEventObject<MouseEvent>) => {
        setMoused(id);
    }

    const onMouseOut = ({ target: { attrs: { id } } }: Konva.KonvaEventObject<MouseEvent>) => {
        setMoused(undefined);
    }

    const onMouseClicked = ({ target: { attrs: { id } } }: Konva.KonvaEventObject<MouseEvent>) => {
        setSelected(selected === id ? undefined : id);
    }



    return (
        <div ref={divRef} style={{ flexGrow: 1, display: 'flex', flexDirection: 'column' }}>
            <Stage height={height - 200} width={width} draggable>
                <Layer>
                    {Object.entries(objs).map(([name, coords]) =>
                        <Line
                            points={coords}
                            fill={selected === name ? '#CC6789' : moused === name ? '#DDD2FF' : '#00D2FF'}
                            stroke='black'
                            key={name}
                            id={name}
                            closed
                            onMouseOver={onMouseOver}
                            onMouseOut={onMouseOut}
                            onClick={onMouseClicked}
                            onTap={onMouseClicked}
                        />
                    )}

                    <Text
                        x={600}
                        y={15}
                        text='lll'
                        fontSize={30}
                        fontFamily='Calibri'
                        fill='black'
                    />

                </Layer>
            </Stage>
        </div>
    );
}

export default withWindowSize(Plan);