import React from 'react';

export interface WithWindowSizeProps {
    height: number;
    width: number;
}

export function withWindowSize<T>(
    WrappedComponent: React.ComponentType<T & WithWindowSizeProps>
  ) {
    // Try to create a nice displayName for React Dev Tools.
    const displayName =
      WrappedComponent.displayName || WrappedComponent.name || "Component";
  
    // Creating the inner component. The calculated Props type here is the where the magic happens.
    return class ComponentWithWindowSize extends React.Component<T, WithWindowSizeProps> {
      public static displayName = `withTheme(${displayName})`;

      state = { width: 0, height: 0 };

      componentDidMount() {
          this.updateWindowDimensions();
          window.addEventListener("resize", this.updateWindowDimensions);
      }

      componentWillUnmount() {
          window.removeEventListener("resize", this.updateWindowDimensions);
      }

      updateWindowDimensions = () => {
          this.setState({ width: window.innerWidth, height: window.innerHeight });
      };
  
      public render() {
 
        // this.props comes afterwards so the can override the default ones.
        return <WrappedComponent {...this.state} {...(this.props as T)} />;
      }
    };
  }